.footer-section {
  height: 424px;
  background-color: #EFEDE8;
  width: 100%;
  text-align: center;
}

.footer-section .partenaires {
  display: inline-block;
  margin-top: 200px;
}

@media (max-width: 767px) {
  .footer-section .partenaires {
    margin-top: 60px;
  }
}

.footer-section .partenaires li {
  display: inline-block;
}

@media (max-width: 767px) {
  .footer-section .partenaires li {
    margin-bottom: 60px;
    display: block;
  }
}

.footer-section .partenaires .supdeco {
  margin-left: 30px;
  margin-right: 30px;
}
